	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://images.pexels.com/photos/196645/pexels-photo-196645.jpeg?w=1260&#x26;h=750&#x26;auto=compress&#x26;cs=tinysrgb\" alt=\"alt text\" title=\"UI Challenge\"></p>\n<p>De følgende designs er baseret på diverse <strong>UI design challenges</strong> på nettet. Når jeg designer disse, forsøger jeg også at inddrage tanker om UX.</p>\n<p><img src=\"https://mir-s3-cdn-cf.behance.net/project_modules/max_1200/b17d5661113341.5a63fb21a7c3f.png\" alt=\"alt text\" title=\"Kitkat Website Mockup\"></p>\n<p>Udfordringen var at skabe et redesign af <strong>Kitkat's</strong> hjemmeside, som skulle præsentere produkter og se moderne ud. Jeg har forsøgt at gøre siden mere stilren med meget whitespace og fokus på produkterne. Samtidig har jeg forsøgt at fjerne det meget firkantede <em>corporate look</em>, som den originale side bar præg af, det er gjort ved at bryde linjerne og gå udover kanterne.</p>\n<p><img src=\"https://mir-s3-cdn-cf.behance.net/project_modules/1400/58e43d61141379.5a65217ea09dd.png\" alt=\"alt text\" title=\"Yamaha Mobile Mockup\"></p>\n<p>Udfordringen var at skabe en produktside til <strong>Yamaha</strong>. Det nye design har en meget ren opsætning, som følger nogle af <strong>Google Material Design</strong> reglerne for at skabe hurtigt overblik på en lille flade.</p>\n<blockquote>\n<p><a href=\"https://www.behance.net/chrisecker3eab\">Alle design kan findes på min Behance her</a></p>\n</blockquote>","frontmatter":{"date":"July 12, 2017","path":"/ui","title":"Diverse UI & UX designs"}}},"pathContext":{}}

